import styled from "styled-components";
import { IoAnalyticsOutline ,IoSettingsSharp } from 'react-icons/io5';
import {
    MdDashboardCustomize,
    MdPersonOutline,
    MdOutlineListAlt,
    MdOutlineMailOutline,
    MdOutlineShoppingBag,
    MdOutlineLogout
} from 'react-icons/md';
import { FaMagento } from 'react-icons/fa';
import { FiPlus } from 'react-icons/fi';
import { RiErrorWarningLine } from 'react-icons/ri'


export const SideBarContainer = styled.aside`
    height: 100vh;


`;

export const SideBarTop = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.4rem;
    background: white;
     

`;

export const LogoIcon = styled(FaMagento)`
    width: 2rem;
    height: 2rem;
    color: ${({ theme }) => theme.colors.danger};
`;

export const LogoWrapper = styled.div`
    display: flex;
    align-items: center;
    gap: .8rem;

`;

export const CloseIconWrapper = styled.div`
    display: none;

`;


export const SideBarItems = styled.div`
    display: flex;
    flex-direction: column;
    height: 85vh;
    position: relative;
    top: 3rem;
    background: #fff;
`;

export const SideBarTitle = styled.h3`
    font-weight: 500;
    font-size:0.87rem;

`;

export const SideBarItem = styled.a`
    color:${({ theme }) => theme.colors.infoDark};
    display: flex;
    margin-left: 2rem ;
    gap:1rem;
    align-items: center;
    position: relative;
    height: 3rem;
    /* font-size: ; */
    transition: all .3s ease ;
    cursor: pointer;
    &:last-child{
        position: absolute;
        bottom: 2rem;
        width: 100%;
    }

`;


export const Counter = styled.span`


`;


export const LogoTitleWrapper = styled.div`

`;

export const LogoTitleH1 = styled.h2`


`;

export const LogoTitleSpan = styled.span`
    color: ${({ theme }) => theme.colors.danger};
`;



/// icons

export const DashboardIcon = styled(MdDashboardCustomize)`
    font-size: 1.6rem;
`;

export const CustomersIcon = styled(MdPersonOutline)`
    font-size: 1.6rem;
`;

export const OrdersIcon = styled(MdOutlineListAlt)`
    font-size:1.6rem;

`;

export const AnalyticsIcon = styled(IoAnalyticsOutline)`
    font-size:1.6rem;
`;

export const MessagesIcon = styled(MdOutlineMailOutline)`
    font-size: 1.6rem;
`;

export const ProdctIcons = styled(MdOutlineShoppingBag)`
    font-size: 1.6rem;
`;

export const RepprotIcon = styled(RiErrorWarningLine)`
    font-size: 1.6rem;
`;

export const SettingsIcon = styled(IoSettingsSharp)`
    font-size: 1.6rem;
`;

export const AddIcon= styled(FiPlus)`
    font-size: 1.6rem;
`;

export const LogoutIcon = styled(MdOutlineLogout)`
    font-size: 1.6rem;
`;